.user-card-additional-info {
  padding-left: 15px;
}

.user-card-additional-info span {
  padding-left: 10px;
  display: inline-block;
  position: relative;
  top: -2px;
}

.user-form-wrapper {
  padding: 30px;
  width: 300px;
}

.user-form-file-label {
  display: block;
  text-align: center;
  cursor: pointer;
  height: 150px;
  overflow: hidden;
}

.user-form-file-label img {
  display: inline-block;
  max-height: 100%;
}
